import React, { useEffect, useState } from "react";

import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";

import { HashLink } from "react-router-hash-link";
import { BrowserRouter as Router } from "react-router-dom";

import logo from "../assets/img/BMine.png";
import linkedin from "../assets/img/nav-icon1.svg";
// import facebook from "../assets/img/nav-icon2.svg";
// import instagram from "../assets/img/nav-icon3.svg";
import github from "../assets/img/githubNav.png";

function NavBar() {
	const [activeLink, setActiveLink] = useState("home");
	const [scrolled, setScrolled] = useState(false);

	useEffect(() => {
		const onScroll = () => {
			if (window.scrollY > 50) {
				setScrolled(true);
			} else {
				setScrolled(false);
			}
		};

		window.addEventListener("scroll", onScroll);

		return () => window.removeEventListener("scroll", onScroll);
	}, []);

	function onUpdateActiveLink(activeLink) {
		setActiveLink(activeLink);
	}

	return (
		<Router>
			<Navbar expand="md" className={scrolled ? "scrolled" : ""}>
				<Container>
					<Navbar.Brand href="#home">
						<img src={logo} alt="Logo" className="navbar_logo" />
					</Navbar.Brand>
					<Navbar.Toggle aria-controls="basic-navbar-nav">
						<span className="navbar-toggler-icon"></span>
					</Navbar.Toggle>
					<Navbar.Collapse id="basic-navbar-nav">
						<Nav className="me-auto">
							<Nav.Link
								href="#home"
								className={
									activeLink === "home" ? "active navbar-link" : "navbar-link"
								}
								onClick={() => onUpdateActiveLink("home")}
							>
								Home
							</Nav.Link>
							<Nav.Link
								href="#skills"
								className={
									activeLink === "skills" ? "active navbar-link" : "navbar-link"
								}
								onClick={() => onUpdateActiveLink("skills")}
							>
								Skills
							</Nav.Link>
							<Nav.Link
								href="#projects"
								className={
									activeLink === "projects"
										? "active navbar-link"
										: "navbar-link"
								}
								onClick={() => onUpdateActiveLink("projects")}
							>
								Projects
							</Nav.Link>
						</Nav>
						<span className="navbar-text">
							<div className="social-icon">
								<a
									target="_blank"
									rel="noopener noreferrer"
									href="https://www.linkedin.com/in/krystian-kruszelnicki-bmine/"
								>
									<img src={linkedin} alt="linkedin" />
								</a>
								<a
									target="_blank"
									rel="noopener noreferrer"
									href="https://github.com/Croosheck"
								>
									<img src={github} alt="github" />
								</a>
								{/* <a>
									<img src={navIcon2} alt="facebook" />
								</a>
								<a>
									<img src={navIcon3} alt="instagram" />
								</a> */}
							</div>
							<HashLink to="#connect">
								<button className="vvd">
									<span>Let's Connect</span>
								</button>
							</HashLink>
						</span>
					</Navbar.Collapse>
				</Container>
			</Navbar>
		</Router>
	);
}

export default NavBar;
